<!---->
<!---->
<style lang='scss' rel='stylesheet/scss'>
@import "../../../utils/mixins/common";

#course-manage-addcourse-container {
    @extend %content-container;

    h2 {
        margin-bottom: 10px;
    }

    .tab {
        max-width: 700px;
    }
    .el-tab-pane {
        max-width: 700px;
    }
    .el-upload-list__item {
        position: relative;
        .el-icon-close {
            position: absolute;
            right: 5px;
            top: 5px;
        }
    } // 
    .testing-set {
        .el-form {
            padding-top: 17px;
            &:first-of-type {
                padding-top: 0;
            }

            hr {
                position: absolute;
                width: 100%;
                border-top: none;
                border-color: #bbb;
            }
            h5 {
                font-size: 14px;
                color: #666;
            }
            .el-input {
                display: inline-block;
                width: 60%;
                vertical-align: middle;
            }

            .multy-choose-item {
                margin: 5px 0;
                &:last-of-type {
                    margin-bottom: 0;
                }
            }
        }

        .bottom-btns {
            .submit {
                float: right;
            }
        }
    }
}
</style>

<template>
    <article id="course-manage-addcourse-container">
        <el-tabs v-model="activeTab" class="tab">
            <el-tab-pane label="" name="first">
                <el-form label-width="120px" ref="formFirst" :rules="rulesFirst" :model="fetchParam">
                    <el-form-item label="" prop="category_id">
                        <CourseCategorySelect type="course" :placeholder="fetchParam.category_name" :autoClear="true" :showNotCat="false" v-model="fetchParam.category_id"></CourseCategorySelect>
                    </el-form-item>
                    <el-form-item label="" prop="course_name">
                        <el-input v-model="fetchParam.course_name"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="type">
                        <el-select v-model="fetchParam.type" placeholder="">
                            <el-option label="" value="private"></el-option>
                            <el-option label="" value="public"></el-option>
                            <el-option label="" value="industry"></el-option>
                            <el-option label="" value="gov"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item  label=""  :fetch-suggestions="querySearch">
                        <!--<el-select clearable v-model="fetchParam.experts_id" placeholder="">
                            <el-option v-for="(item, index) in experts_list" :label="item.name" :value="item.id" :key="item.id">
                            </el-option>
                        </el-select>-->
                        <Experts v-model="fetchParam.experts_id" :placeholder="fetchParam.experts_name"
                                            v-on:change="val=>fetchParam.experts_id=val" :change="getExpertsList" :list="changelist">
                        </Experts>
                    </el-form-item>
                    <el-form-item label="" prop="image">
                        <img :src="fetchParam.image | fillImgPath" width="200" height="112" v-show="fetchParam.image">
                        <CropperImg ref="imgcropper" :confirmFn="cropperImgSucc" :aspectRatio="16/9"></CropperImg>
                    </el-form-item>
                    <el-form-item label="" prop="material_type">
                        <el-select v-model="fetchParam.material_type" @change="typeChange" placeholder="" >
                            <el-option label="" value="video"></el-option>
                            <el-option label="WORD" value="doc"></el-option>
                            <el-option label="PPT" value="ppt"></el-option>
                            <el-option label="PDF" value="pdf"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="material_id">
                        <UploadFile :onSuccess="handleUploadDoc" :url="uploadDocUrl" :accept="accept" :disabled="fetchParam.material_type == null" v-show="fetchParam.material_type !== 'video'"></UploadFile>
                        <el-button v-show="fetchParam.material_type === 'video'" @click="getVideoName">
                            <i>{{fetchParam.material_name}}</i>
                        </el-button>
                        <el-button v-show="fetchParam.material_type === 'video'" @click="preview(fetchParam.material_id)" type="text" size="big"
                               > 
                        </el-button>
                    </el-form-item>
                    <!--<el-form-item label="">
                        <CourseAlbumSelect :placeholder="fetchParam.album_name" v-model="fetchParam.albumid"></CourseAlbumSelect>
                    </el-form-item>-->
                    <el-form-item label="" prop="description">
                        <el-input v-model="fetchParam.description" type="textarea" :autosize="{ minRows: 4, maxRows: 6}" placeholder="">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <vTags v-model="courseTags"></vTags>
                    </el-form-item>
                    <el-form-item label="" prop="sort">
                        <el-input v-model="fetchParam.sort" type="number" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="share">
                        <el-radio class="radio" v-model="fetchParam.share" :label="1"></el-radio>
                        <el-radio class="radio" v-model="fetchParam.share" :label="0"></el-radio>
                    </el-form-item>

                    <h2></h2>
                    <el-form-item label="" prop="need_testing">
                        <el-radio class="radio" v-model="fetchParam.need_testing" :label="1"></el-radio>
                        <el-radio class="radio" v-model="fetchParam.need_testing" :label="0"></el-radio>
                    </el-form-item>
                    <el-form-item label="" prop="limit_time">
                        <el-input-number :min="0" size="small" :disabled="fetchParam.need_testing == 0" placeholder="" v-model="fetchParam.limit_time"></el-input-number>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input :disabled="fetchParam.need_testing == 0" placeholder="0" v-model="fetchParam.limit_repeat"></el-input>
                    </el-form-item>
                    <!-- <el-form-item label="" prop="score_pass">
                        <el-input :disabled="fetchParam.need_testing == 0" v-model="fetchParam.score_pass"></el-input>
                    </el-form-item> -->

                    <el-form-item label="" v-if="!readonly">
                        <el-button style="float: right" type="primary" @click="btnNextClick">
                            <i>{{ fetchParam.need_testing == 0 ? '' : '' }}</i>
                        </el-button>
                    </el-form-item>
                </el-form>
            </el-tab-pane>
            <el-tab-pane :disabled="!fetchParam.contentid" label="" name="second" class="testing-set">
                <el-form>
                    <el-form label-width="120px" v-for="(item,index) in fetchTesting" :key="index">
                        <el-form-item label="" v-if="!readonly">
                            <el-button icon="plus" @click='addTesting(0, index)'></el-button>
                            <el-button icon="plus" @click='addTesting(1, index)'></el-button>
                            <el-button icon="plus" @click='addTesting(2, index)'></el-button>
                            <el-button icon="delete" type="danger" @click='deleteTesting(index, item)'></el-button>
                        </el-form-item>
                        <el-form-item :label="'' + (index+1) + ''">
                            <span v-if="item.category == 0"></span>
                            <span v-else-if="item.category == 1"></span>
                            <span v-else></span>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="item.description" :disabled="!item.editable" type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder="">
                            </el-input>
                        </el-form-item>
                        <!-- <el-form-item label="">
                            <el-input placeholder="" :disabled="!item.editable" v-model="item.score"></el-input>
                        </el-form-item> -->
                        <el-form-item label="">
                            <UploadImg :defaultImg="item.image" :url="uploadImgUrl" :disabled="!item.editable" :onSuccess="res => item.image = res.data.url"></UploadImg>
                        </el-form-item>

                        <!---->
                        <el-form-item label="" v-if="item.category == 0">
                            <el-radio class="radio" :disabled="!item.editable" v-model="item.correct" :label="1">
                                <i></i>
                            </el-radio>
                            <el-radio class="radio" :disabled="!item.editable" v-model="item.correct" :label="0">
                                <i></i>
                            </el-radio>
                        </el-form-item>

                        <!--|-->
                        <el-form-item label="" v-else>
                            <h5></h5>
                            <div class="multy-choose-item" v-for="(option,indexOption) in item.options" :key="indexOption">
                                <el-checkbox v-model="option.correct" :true-label="1" :disabled="!item.editable" v-if="item.category == 2"></el-checkbox>
                                <el-radio class="radio" v-model="item.correct" :label="indexOption" :disabled="!item.editable" v-else>
                                    <i></i>
                                </el-radio>
                                <el-input placeholder="" v-model="option.description" :disabled="!item.editable"></el-input>
                                <el-button :disabled="!item.editable" type="text" @click="item.options.splice(indexOption, 1)">
                                    <i></i>
                                </el-button>
                            </div>
                            <div class="multy-choose-item">
                                <el-button v-if="item.editable" type="text" @click="addMoreTestingOption(item.options)"></el-button>
                            </div>
                        </el-form-item>

                        <el-form-item label="">
                            <el-input v-model="item.explain" :disabled="!item.editable" type="textarea" :autosize="{ minRows: 4, maxRows: 6}" placeholder="">
                            </el-input>
                        </el-form-item>

                        <hr>
                    </el-form>
                </el-form>

                <el-form label-width="120px" v-if="!readonly">
                    <el-form-item label="">
                        <el-button icon="plus" @click='addTesting(0, fetchTesting.length)'></el-button>
                        <el-button icon="plus" @click='addTesting(1, fetchTesting.length)'></el-button>
                        <el-button icon="plus" @click='addTesting(2, fetchTesting.length)'></el-button>
                    </el-form-item>
                </el-form>

                <div class="bottom-btns" v-if="!readonly">
                    <el-button @click="btnPreClick"></el-button>
                    <el-button class="submit" type="primary" @click="handleSubmitTesting"></el-button>
                </div>
            </el-tab-pane>
        </el-tabs>

        <DialogVideo :onSelect="handleVideoSelected" v-model="isShowVideoDialog"></DialogVideo>

        <VideoPreview :url="videoUrl" :onchange="fetchVideoData" ref="videoPreview"></VideoPreview>
    </article>
</template>

<script>
import courseService from '../../../services/course/courseService.js'
import videoService from '../../../services/course/videoService.js'
import expertsService from '../../../services/course/expertsService.js'
import UploadImg from '../../component/upload/UploadImg.vue'
import CropperImg from '../../component/upload/ImagEcropperInput.vue'
import DialogVideo from '../component/DialogVideo.vue'
import UploadFile from '../../component/upload/UploadFiles.vue'
import CourseCategorySelect from '../../component/select/CourseCategory.vue'
import CourseAlbumSelect from '../../component/select/CourseAlbum'
import testingFactory from '../utils/testingFactory'
import formUtils from '../../../utils/formUtils'
import {transformParam} from '../../../utils/common'
import vTags from '../../component/form/Tags.vue'
import VideoPreview from '../../component/dialog/VideoPreview.vue'
import Experts from '../../component/select/Experts'

export default {
    name: 'course-manage-addcourse',
    data() {
        return {
            activeTab: 'first',
            uploadDocUrl: '', // url
            isShowVideoDialog: false, // 
            fetchParam: getOrignData(),
            courseTags: [],
            rulesFirst: { // 
                course_name: { required: true, message: '', trigger: 'change' },
                category_id: { required: true, type: 'number', message: '', trigger: 'change' },
                // image: { required: true, message: '', trigger: 'change' },
                description: { required: true, message: '', trigger: 'change' },
                material_id: { required: true, type: 'number', message: '', trigger: 'change' },
                need_testing: { required: true, type: 'number', message: '', trigger: 'change' },
                type: { required: true,  message: '', trigger: 'change' },
                material_type: { required: true,  message: '', trigger: 'change' },
            },
            accept: '*.doc,*.docx', // 
            // 
            fetchTesting: [],
            readonly: false, // 
            videoUrl: '', // url
            // experts_list:[],
            changelist:{}
        }
    },

    created() {
        this.uploadDocUrl = courseService.getCourseDocUploadUrl()
        this.uploadImgUrl = courseService.commonUploadImage()

        //
        if (this.$route.params.courseInfo) {

            this.activeTab= 'first'
            // this.fetchParam = this.$route.params.courseInfo   //
            for(let i in this.$route.params.courseInfo){
                 this.fetchParam[i]=this.$route.params.courseInfo[i]
            }
            console.log(this.fetchParam)
            // console.log(this.fetchParam.category_name,this.$route.params.courseInfo.category_name)
            this.fetchParam.material_name= this.$route.params.courseInfo.course_name
            // this.fetchParam.experts_id = this.$route.params.courseInfo.experts_name
            
            this.courseTags = this.fetchParam.tags ? this.fetchParam.tags.split(',') : []
            xmview.setContentTile('-')
        } else if (this.$route.query.contentid) {
            this.activeTab= 'first'
            courseService.getCourseInfo({ course_id: this.$route.query.contentid }).then((ret) => {
                this.fetchParam = ret.course                  //  
                this.fetchParam.course_name= this.$route.params.courseInfo.course_name
                this.fetchParam.material_name= this.$route.params.courseInfo.course_name
                this.courseTags = this.fetchParam.tags ? this.fetchParam.tags.split(',') : []
                xmview.setContentTile('-')
            }).catch((ret) => {
                xmview.showTip('error', ret.message)
            })
        }

        this.$route.params.tab && (this.activeTab = this.$route.params.tab)
        this.readonly = this.$route.params.readonly
        xmview.setContentLoading(false)
        this.getExpertsList()

    },

    watch: {
        'fetchParam.need_testing'(val) {
            if (val == 1) { // 
                this.rulesFirst.limit_time = { required: true, message: '', trigger: 'change' }
                this.rulesFirst.score_pass = { required: true, message: '', trigger: 'change' }
            } else { // 
                this.$delete(this.rulesFirst, 'limit_time')
                this.$delete(this.rulesFirst, 'score_pass')
            }
        },
        'activeTab'(val) {
            // if (val === 'second' && this.fetchTesting.length < 1 && this.fetchParam.contentid) {
            if (val === 'second'  && this.fetchParam.contentid) {
                xmview.setContentLoading(true)
                courseService.getTestingInfo({ course_id: this.fetchParam.contentid }).then((data) => {
                    this.fetchTesting = data
                    this.fetchTesting.forEach((item) => {
                        if (item.category == 1) {
                            item.options.forEach((optionItem, index) => {
                                if (optionItem.correct == 1) item.correct = index
                            })
                        }
                    })
                    xmview.setContentLoading(false)
                })
            }
        },
        'fetchParam.albumid'(val) {
            // id  
            if (!val) {
                this.fetchParam.album_name = void 0
                this.fetchParam.albumid = void 0
            }
        }
    },
    methods: {
        getExpertsList (val, length) {
                return expertsService.fetchExpertsData({
                    name: val,
                    // category: this.type,
                    pagesize: this.pageSize,
                    page: parseInt(length / this.pageSize) + 1
                }).then((ret) => {
                    this.$emit('changelist', ret)
                    this.changelist = ret;
                    return ret
                })
            },
        //
            querySearch(queryString, cb) {
                var restaurants = this.restaurants;
                var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
                //  callback 
                cb(results);
            },
        fetchVideoData () {
                this.loadingData = true
                return videoService.getVideo(this.fetchParam).then((ret) => {
                    this.data = ret.data
                    this.total = ret._exts.total
                    this.loadingData = false
                })
            },
         // 
            preview (index) {
                // 
                videoService.getVideoPreviewUrl(index).then((ret) => {
                    this.videoUrl = ret.video
                    // this.row = row
                    this.$refs.videoPreview.show(this.fetchParam.material_name) //
                })
            },

        // 
        getVideoName(){
            this.isShowVideoDialog=true
        },
        // 
        btnNextClick() {
            this.fetchParam.limit_time && (this.fetchParam.limit_time += '')
            this.fetchParam.score_pass && (this.fetchParam.score_pass += '')
            this.fetchParam.tags = this.courseTags ? this.courseTags.join(',') : ''
            this.$refs.formFirst.validate((isValidate) => {
                if (!isValidate) return
                transformParam(this.fetchParam)
                //  console.log(this.fetchParam)
                // for(let i in this.fetchParam){
                //     this.fetchParam[i] = this.fetchParam[i]== undefined ?'': this.fetchParam[i]
                    
                // }
                console.log(this.fetchParam)
                let p
                // 
                if (this.fetchParam.contentid) {
                    p = courseService.editCourse(this.fetchParam).then((ret) => {
                        this.activeTab = 'second'
                    })
                } else {
                    p = courseService.addCourse(this.fetchParam).then((ret) => {
                        this.fetchParam.contentid = ret.contentid
                        this.activeTab = 'second'
                    })
                }
                
                p.then(() => {
                    //   
                    if (this.fetchParam.need_testing === 0) {
                        this.$router.back()
                    }
                })
            })
        },
        btnPreClick() {
            this.activeTab = 'first'
        },
        // 
        handleUploadMedia(response) {
            this.fetchParam.material_id = response.data.contentid
        },
        // 
        cropperImgSucc(imgData) {
            courseService.commonUploadImageBase({ image: imgData ,extpath:''}).then((ret) => {
                this.fetchParam.image = ret.url
            })
        },
        // 
        handleVideoSelected(row) {
            this.fetchParam.material_name = row.title
            this.fetchParam.material_id = row.material_id
        },
        addTesting(type, index) {
            this.fetchTesting.splice(index, 0, testingFactory.getTestingSet(type))
        },
        // 
        deleteTesting(index, item) {
            xmview.showDialog(` <i style="color:red">${item.description || ''}</i> ?`, () => {
                if(!item.course_id){
                     this.fetchTesting.splice(index, 1)
                }else{
                     courseService.delCourse({course_id:item.course_id,id:item.id}).then((ret) => {
                       this.fetchTesting.splice(index, 1)
                    })
                }
               
            })
        },
        //  
        addMoreTestingOption(options) {
            options.push({
                description: '',
                correct: ''
            })
        },
        // 
        handleSubmitTesting() {
            // 
            let item = null

            if (!this.fetchTesting || this.fetchTesting.length < 1) {
                this.$router.back()
                return
            }
            let requestParam = JSON.parse(JSON.stringify(this.fetchTesting))
            for (let i = 0; i < requestParam.length, item = requestParam[i]; i++) {
                // 
                if (item.category == 1 && typeof item.correct == 'number') {
                    if(item.options){
                        item.options.map((itemOptions) => {
                            delete itemOptions.correct
                        })
                        item.options[item.correct].correct = 1
                        delete item.correct
                    }

                }

                // sort
                item.sort = i + 1
                if (item.options) {
                    item.options.map((itemOptions, index) => {
                        itemOptions.sort = index + 1
                    })
                }
            }
            xmview.setContentLoading(true)
            console.log(encodeURI(formUtils.serializeArray(requestParam)).replace(/\+/g, '%2B'))
            courseService.addOrEditTesting({
                course_id: this.fetchParam.contentid,
                subjects: encodeURI(formUtils.serializeArray(requestParam)).replace(/\+/g, '%2B')
            }).then((ret) => {
                xmview.showTip('success', '')
                this.$router.back()
            }, () => {
            }).then(() => {
                xmview.setContentLoading(false)
            })
        },
        handleUploadDoc(rep) { // 
            this.fetchParam.material_id = rep.data.contentid
        },
        // 
        typeChange(val) {
            if (val === 'doc') {
                this.accept = '.doc,.docx'
            } else if (val === 'ppt') {
                this.accept = '.ppt,pptx'
            } else if (val === 'pdf') {
                this.accept = '.pdf'
            }
        }
    },
    components: { CropperImg, UploadFile, CourseCategorySelect, CourseAlbumSelect, DialogVideo, UploadImg, vTags ,VideoPreview,Experts}
}

function getOrignData() {
    let orignData = { // 
        contentid: void 0,  //tab
        category_id: void 0,
        category_type:void 0, //	1.; 2.
        course_name: void 0,
        name: void 0, //
        image: void 0,
        tags: void 0,
        gov:void 0,
        type: '', //  private,public,industry,gov
        material_type: '', //	video,doc,ppt,pdf
        material_id: void 0,
        material_name: '',
        albumid: void 0,
        album_name: void 0,
        description: void 0,
        need_testing: 0,
        limit_time: void 0,
        limit_repeat: void 0,
        score_pass: void 0,
        price_enabled: void 0,
        price: void 0,
        price_floa: void 0,
        experts_id: void 0,
        experts_name:'',
        sort:void 0,
        share:void 0,
    }

    return orignData
}
</script>



